              lat: self.lat
            };
          } else if (result.state == "denied") {
            header = {};

            self.$swal("", "   يجب تفعيل الوصول للموقع", 'warning')
          } else if (result.state == "prompt") {
            header = {};
            self.$swal("", "   فعل الوصول للموقع رجاءاً   ", 'warning')
          } else {
            header = {};
          }






          axios({
