<script>
    const axios = require('axios');
    const toast = require('vue-toasted');
    import {
        EventBus
    } from '../event-bus.js';



    import {
        userInfo
    } from 'os';



    export default {
        props: ['dialog'],


        data() {
            return {
                email_error: '',
                user_name: '',
                allerros: '',
                from: '',
                phonetoverfie: '',
                signin: true,
                from_post: false,
                valid: false,
                error_msg: '',
                name: '',
                email: '',
                phone: '',
                password: '',
                city: '',
                verfy_code: '',
                user_id: '',
                verfy_Dailog: false,
                images: [],
                urL: "http://109.224.53.155",
                cities: [{
                        id: '1',
                        name: 'Bagdad',


                    },
                    {
                        id: '2',
                        name: 'Karbala',


                    }
                ],
                seigin: true,
                from_booking: false,
                imagesUrl: '',
                auths: false,
                imgname: '',
                user_img: '',
                imageName: '',
                name: '',


                nameRules: [
                    (v) => !!v || 'يجب ادخال الاسم',

                ],


                verfyRules: [
                    (v) => !!v || 'يجب ادخال كود التفعيل',
                    (v) => (v.length == 5) || ' يجب ان يكون كود التفعيل 5 ارقام',

                ],


                phoneRules: [
                    (v) => !!v || 'يجب ادخال رقم الهاتف',
                    (v) => v.length > 10 && v.length < 12 || 'يجب ادخال رقم هاتف صحيح',

                ],



             
                
                emailRules: [



                    (v) => (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,5})+$/.test(v) || v.length < 1) ||
                    'ألايميل غير صحيح',


                ],



                PasswordRules: [
                    (v) => !!v || 'يجب ادخال الباسورد',
                    v => (v.length > 3) || ' يجب ان لا يقل عن 4',

                ],


                user_info: localStorage.getItem('user_info'),
                user_id: localStorage.getItem('user_id'),

                reg_dialog: false,


                data: '',
                desserts: [],
                editedIndex: -1,
                editedItem: {},
                post_dialog: false,
                defaultItem: {


                },

            }
        },

        computed: {

        },

        watch: {

        },
        // reg_dialogl(val) {
        //     val || this.close()
        // },


        created() {

            // Listen for the i-got-clicked event and its payload.

            EventBus.$on('reg_dialog', (from) => {
                this.from = from;
                this.reg_dialog = true;
                this.from_booking = true;

            });


        },
        updated() {

            // if (localStorage.getItem('authenticat') == "false" || localStorage.getItem('authenticat') == null) {

            //     this.auths = false;
            // } else {
            //     this.auths = true;
            //     //      this.Get_Member_Info();

            //     //   this.user_info = localStorage.getItem('user_info');


            // }


        },









        methods: {
            verfyAccount() {
                if (this.$refs.form_validate.validate()) {

                    axios.post(this.urL + '/check_verfitify_number', {

                            phone: "964" + parseInt(this.phonetoverfie),
                            verfitify_number: this.verfy_code,

                        }).then(response => {
                            if (response.data.success == true) {

                                this.$swal('', 'تم  تفعيل الحساب', 'success')
                                this.verfy_Dailog = false;

                                this.signin = false
                                this.reg_dialog = false


                            }

                        })

                        .catch(error => {
                            if (error.response.data.success == false) {


                                this.$swal('', 'رمز التفعيل خطاء', 'error')
                            }

                        });
                }
            },
            signin_fun() {

                this.signin = true

                this.reg_dialog = true

            },


            signup() {
                this.signin = false
                this.reg_dialog = true
                state.index = state.array.indexOf('you')

            },

            logout() {
                localStorage.setItem('user_id', ' ');
                window.localStorage.clear();
                location.reload();
                this.$router.push({
                    name: 'ألرئيسية',
                });

                localStorage.setItem('authenticat', false);
               // localStorage.setItem('access_token_user', response.data.token);
                this.authenticat = false;
                this.auths = false;


                // this.Get_Member_Info();



            },




            pickFile() {
                this.$refs.image.click()
            },



            sginUp() {

                //alert(this.city)

                var data = {
                    owner_name: this.name,
                    name: this.name,
                    email: this.email,
                    password: this.password,
                    phone: "964" + parseInt(this.phone),
                    from: '',
                    address_id: "2",
                    province_id: "1",
                    country_id: this.city,
                    category_id: "1",
                    long: "33",
                    lat: "32",
                    discraption: "d",
                    images: this.images,
                    type: "png"

                };
                // this.description=JSON.stringify(data);
                axios({
                        method: 'post',
                        url: this.urL + "/Auth/v1/signupClient",
                        data: data
                    })
                    .then(response => {
                        this.phonetoverfie = this.phone;

                        this.reg_dialog = false;

                        //this.login();

                        if (response.data.success == true) {
                            this.$refs.form.reset()
                            this.user_id = response.data.user_id;
                            this.verfy_Dailog = true;
                        }
                        // 
                        switch (this.from) {
                            case "mybooking":
                                this.$router.push({
                                    name: 'mybooking'
                                })
                                break;
                            case "fromItem":

                                EventBus.$emit('booking_dialog', this.booking_dialog = true);
                                break;

                                // case 6:
                                //     day = "Saturday";
                        }





                    })
                    .catch(error => {
                        if (error.response.status == 422) {
                            this.$swal("  ", "  الرقم مستخدم", 'error')

                        } else {
                            this.$swal("  ", "  خطاء بالخادم", 'error')
                        }


                    });

            }

            ,



            clear() {

                this.editedItem.email = ''

                this.$validator.reset()

            },







            cats_filter() {


                // Fire.$emit('cats_fliter');
            },







            login() {



                if (this.$refs.form.validate()) {





                    //Sign In      

                    if (this.signin) {

                        axios.post(this.urL + '/Auth/v1/login', {

                                phone: "964" + parseInt(this.phone),
                                password: this.password,

                            }).then(response => {

                               
                                if(response.data.user_role=='client')
                                {


                                 localStorage.setItem('user_info', JSON.stringify(response.data));
                                this.auths = true,
                                this.phonetoverfie = this.phone;
                                this.user_name = response.data.user_name;
                                this.user_img = response.data.user_image;
                                this.reg_dialog = false;

                                localStorage.setItem('access_token_user', response.data.token);
                                this.authenticat = true;
                                localStorage.setItem('authenticat', true);
                                EventBus.$emit('authenticat', true);



                                if (response.data.success == true) {
                                    this.$refs.form.reset()

                                } else if (response.data.success == false) {
                                    if (response.data.error == "not verified") {
                                        this.user_id = response.data.user_id;
                                        this.verfy_Dailog = true;
                                    }

                                }


                                switch (this.from) {
                                    case "mybooking":
                                        this.$router.push({
                                            name: 'mybooking'
                                        })
                                        break;
                                    case "fromItem":

                                        EventBus.$emit('booking_dialog', this.booking_dialog = true);
                                        break;

                                        // case 6:
                                        //     day = "Saturday";
                                }

                                toast({
                                    type: 'success',
                                    title: 'تم تسجيل الدخول بنجاح'
                                })



                                }

                                else
                                {
                                    this.error_msg = 'يجب ان يكون حساب يوزر';    
                                }

                              




                                


                            })
                            .catch(error => {

                                this.error_msg = 'رقم الهاتف او الباسورد غير صحيح';
                                this.phonetoverfie = this.phone;
                                if (error.response.data.error == "not verified") {
                                    this.user_id = error.response.data.user_id;
                                    this.verfy_Dailog = true;
                                }


                            }).finally((w) => {

                                EventBus.$emit('getUserReservationsCount', true);
                            });






                        //Sign Up

                    } else {
                      
                        this.error_msg=''; 
                        this.sginUp();



                    }





                }



            },
            close() {

                this.imageName = ''
                this.imageFile = ''
                this.imageUrl = ''


                //this.emailRules.splice(2);
                this.allerros = ' ';
                this.error_msg = ' ',
                    this.editedItem = [],


                    this.$refs.form.reset()
                this.reg_dialog = false

            },



            pickFile() {

                this.$refs.image.click()
            },

            onFilePicked(e) {

                const files = e.target.files
                if (files[0] !== undefined) {
                    this.imageName = files[0].name
                    if (this.imageName.lastIndexOf('.') <= 0) {
                        return
                    }
                    const fr = new FileReader()
                    fr.readAsDataURL(files[0])
                    fr.addEventListener('load', () => {
                        this.imageUrl = fr.result
                        this.imageFile = files[0]
                        this.images = [];
                        this.images.push(fr.result);
                        this.imgname = files[0].name;


                    })

                } else {
                    this.imageName = ''
                    this.imageFile = ''
                    this.imageUrl = ''
                }
            },
        },


        mounted() {

             if (localStorage.getItem('authenticat') == "false") {
                this.auths = false;
            } else if (localStorage.getItem('authenticat') == "true") {


                var userinfo = JSON.parse(localStorage.getItem('user_info'));
                this.user_name = userinfo.user_name;
                this.user_img = userinfo.user_image;

                this.auths = true;
            } else if (localStorage.getItem('authenticat') == null) {
                this.auths = false;
            }

            //localStorage.setItem('authenticat', false);



        }

    }
</script>