  const axios = require('axios');
  import {
    EventBus
  } from '../event-bus.js';
  export default {
    props: ['item'],

    data: () => ({

      model: 0,
      e1: 1,
      rating: 4,
      fawater_res_Dailog: false,
      rating_dailog: false,
      valid_day: true,
      valid_pay: true,
      date: '',
