        phone: "",


      },

      sechdual_info: []

    }),
    mounted() {
      // 
      this.token = localStorage.getItem('access_token_user');

      this.id = this.$route.params.id;
      this.getItemDetails();
    },

    created() {

      //  EventBus.$emit('booking_dialog', this.booking_dialog=true);

      // EventBus.$on('booking_dialog', booking_dialog => {


      //   this.booking_dialog = true;
      //   //  alert(this.booking_dialog);

      // });

      // EventBus.$on('authenticat', authenticat => {

      //   this.auths = true;

      // });



    },
    methods: {

      close() {
        this.booking_dialog = false;
        this.$refs.form_fa.reset()
        this.e1 = 1;
       
      

      },

      step1() {



        this.errors = [];
        if (!this.date || !this.time) {
          this.$refs.form.validate();

        } else {

          this.e1 = 2;
        }


      },





      GetOffersDays() {






        //  var days = ["اﻷحد", "اﻷثنين", "الثلاثاء", "اﻷربعاء", "الخميس", "الجمعة", "السبت"];



        for (var i = 0; i < this.offers.length; i++) {


          //alert(this.offers[i]['offer_to_date']);
          var from_date = new Date(this.offers[i]['offer_from_date']);
          var to_date = new Date(this.offers[i]['offer_to_date']);
          // var new_date = new Date(this.offers[i]['offer_to_date']);

          var Difference_In_Time = to_date.getTime() - from_date.getTime();

          // To calculate the no. of days between two dates 
          var Difference_In_Days = Difference_In_Time / (1000 * 3600 * 24);
          //alert(Difference_In_Days);
          //alert(Difference_In_Days);
          for (i = 0; i < Difference_In_Days + 1; i++) {
            // alert(i);
            var x = from_date.getDay() + i;
            if (x > 6) {
              // this.dayoffer.push(days[x-7]);
              this.dayoffer.push(x - 7);



            } else {
              // this.dayoffer.push(days[x]);

              this.dayoffer.push(x);
            }


            //alert(x);




          }

          // alert(from_date.getDay());   0  17

          //  alert(to_date.getDay());   2 22



          //  this.dayoffer.push(days[from_date.getDay()]);

          //  this.dayoffer.push(days[to_date.getDay()]);
          //alert(this.dayoffer[1]);
          // this.dayoffer.push(days[to_date.getDay()]);
          // alert(from_date.getDay())
          // alert(to_date.getDay())
          // alert(this.offers[i]['offer_from_date']);
          // alert(days[date.getDay()]);


        }
      },

      validate_fawater() {
        if (this.$refs.form_fa.validate()) {
          this.fatwra_dialog = false;
        }
      },

      getItemDetails() {


        var url = this.urL + "/getItemBy/" + this.id;
        axios.get(url).then(response => {
          this.info = response.data.Data[0];
          this.iMages = this.info.images;

          this.address = response.data.Data[0].address.province.province_name;
          this.offers = response.data.Data[0].offers;
          this.reserv_date = response.data.Data[0].reservation;



          this.GetOffersDays();
          this.getDate_andTime(this.id);

        })
      },

      getDate_andTime(id) {

        var url = this.urL + "/getDaysTime/" + id;
        axios.get(url).then(response => {
          this.booking_schedual = response.data.Data;
          // var times_text = [
          //   "1:00 ص", "2:00 ص", "3:00 ص", "4:00 ص ", "5:00 ص", "6:00 ص", "7:00 ص ", "8:00 ص", "9:00 ص", "10:00 ص",
          //   "11:00 ص", "12:00 ص", "1:00 م", "2:00 م", "3:00 م", "4:00 م ", "5:00 م", "6:00 م", "7:00 م ",
          //   "8:00 م", "9:00 م",
          //   "10:00 م", "11:00 م", "12:00 م",
          // ];

          for (var day = 0; day < 7; day++) {

            var offer = '';
            for (var i = 0; i < this.dayoffer.length; i++) {

              if (this.dayoffer[i] == day) {

                offer = 1;

              }



            }

            this.sechdual_info.push({
              day_name: this.getdays(day + 1),
              time_duration: ['غير متوفر'],
              status: false,
              offer: offer



            });
            for (i = 0; i < this.booking_schedual.length; i++) {
              if (day >= this.booking_schedual[i].from_day && day <= this.booking_schedual[i].to_day) {

                var t = [];

                for (var time = 0; time < this.booking_schedual[i].time_to_open.length; time++) {


                  var from_time = this.booking_schedual[i].time_to_open[time].from_time.split(":");
                  var totime = this.booking_schedual[i].time_to_open[time].to_time.split(":");



                  t.push(from_time[0] + ":" + from_time[1] + " - " +
                    totime[0] + ":" + totime[1])

                }

                this.sechdual_info[day].time_duration = t
                this.sechdual_info[day].status = true



                this.timeline_status[day] = true;

              }
            }



          }



        })

      },
      allowedDates(val) {


        var mydate = new Date(val);
        var day = mydate.getDay();

        var result = false
        for (var i = 0; i < this.booking_schedual.length; i++) {

          if (day >= this.booking_schedual[i].from_day - 1 && day <= this.booking_schedual[i].to_day - 1) {

            result = true;
          }
          if (day == 6 && this.booking_schedual[i].from_day == 0) {

            result = true;
          }
        }


        if (this.booking_schedual[0].every_day == 1) {
          result = true;
        }
        //         for(var a=0;a<this.reserv_date.length;a++){


        //    if (val == this.reserv_date[a].from_date ) {

        //             result = false;
        //           }


        // }


        var hourResv = this.chackDateCompletReservation();
        var hour_valid = 0;
        for (var a = 0; a < this.reserv_date.length; a++) {

          var hours = this.reserv_date[a].from_time.split(":");
          var hour = parseInt(hours[0]);

          for (var b = 0; b < hourResv.length; b++) {




            if (hourResv[b] == hour && val == this.reserv_date[a].from_date) {
              hour_valid++;

            }
          }

        }

        if (hourResv.length == hour_valid) {
          result = false;
        }
        //chack inisite date available
        var d1 = new Date();
        var d2 = new Date(val)
        if (result == true && d2 > d1) {
          // this.date =new Date().toISOString().substr(0, 10);

        } else {

          //  var dt = new Date();

          //         dt.setMonth( dt.getMonth() + 1 );

          //          this.date =dt.toISOString().substr(0, 10);
        }
        return result;

      },

      chackDateCompletReservation() {
        var houresReserv = [];
        for (var h = 0; h < 24; h++) {
          for (var i = 0; i < this.booking_schedual.length; i++) {
            for (var ii = 0; ii < this.booking_schedual[i].time_to_open.length; ii++) {

              var hours = this.booking_schedual[i].time_to_open[ii].from_time.split(":");
              var hour = parseInt(hours[0]);

              var hours1 = this.booking_schedual[i].time_to_open[ii].to_time.split(":");
              var hour1 = parseInt(hours1[0]);

              if (h >= hour && h <= hour1 || this.booking_schedual[i].every_day == 1) {

                houresReserv.push(h);
              }
            }

          }
        }
        return houresReserv;
      },


      allowedHours(v) {



        var result = false;
        for (var i = 0; i < this.booking_schedual.length; i++) {
          for (var ii = 0; ii < this.booking_schedual[i].time_to_open.length; ii++) {

            var hours = this.booking_schedual[i].time_to_open[ii].from_time.split(":");
            var hour = parseInt(hours[0]);

            var hours1 = this.booking_schedual[i].time_to_open[ii].to_time.split(":");
            var hour1 = parseInt(hours1[0]);



            if (v >= hour && v <= hour1 && (this.booking_schedual[i].from_day == this.dayselectid || this
                .booking_schedual[i].every_day == 1)) {

              result = true;
            }
          }

        }




        for (var a = 0; a < this.reserv_date.length; a++) {

          hours = this.reserv_date[a].from_time.split(":");
          hour = parseInt(hours[0]);


          if (v == hour && this.date == this.reserv_date[a].from_date) {

            result = false;
          }
        }



        return result;



      },

      allowedMinutes(v) {
        var result = false;
        for (var i = 0; i < this.booking_schedual.length; i++) {
          for (var ii = 0; ii < this.booking_schedual[i].time_to_open.length; ii++) {

            var hours = this.booking_schedual[i].time_to_open[ii].from_time.split(":");
            var hour = parseInt(hours[1]);

            var hours1 = this.booking_schedual[i].time_to_open[ii].to_time.split(":");
            var hour1 = parseInt(hours1[1]);

            if (v >= hour && v <= hour1) {

              result = true;
            }
          }

        }

        return true;
      },

