    methods: {

      getlocation(lat, lng) {
        this.info.address.lat = lat;
        this.info.address.long = lng

        this.map_dialog = false;


      },



      delete_img(img_id, index) {


        const Swal = require('sweetalert2');



        Swal.fire({
          title: "هل انت متاكد من الحذف ؟",

          icon: "warning",
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          confirmButtonText: 'Yes, delete it!'

        }).then((result) => {
          if (result.value) {
            this.img_cliced = index;

            var url = this.urL + "/delete_item_img/" + img_id;
            axios({
              method: 'get',
              url: url,
              headers: {
                token: this.token
              }

            }).then(response => {


              this.info.images[index].image_path = null;
              this.img_name = index;
              this.images[index] = null;

              Swal.fire(
                'تم الحذف بنجاح',
                '',
                'success'
              )

            }).catch(error => {
              this.$swal('خطاء', "خطاء بالاتصال", 'error')
            }).finally(d => {

              this.getitems();
            });



          }
        })




      },

      cancel() {
        this.$emit("changeStatus", false);
      },
      getItemDetails() {

        var url = this.urL + "/getItemBy/" + this.itemId;
        axios.get(url).then(response => {
          this.info = response.data.Data[0];
          this.imgs = this.info.images;
          this.address = response.data.Data[0].address.province.province_name;

          for (var i = 0; i < 4; i++) {
            this.images[i] = this.urL + "/" + this.info.images[i].image_path;

          }

          for (var a = 0; a < this.info.day_to_open.length; a++) {

            this.day_to_open.push({

              every_day: this.info.day_to_open[a].every_day,
              every_time: this.info.day_to_open[a].every_time,
              from_day: this.info.day_to_open[a].from_day,
              from_time: this.info.day_to_open[a].from_time,
              to_day: this.info.day_to_open[a].to_day,
              to_time: this.info.day_to_open[a].to_time,
              max_requests: this.info.day_to_open[a].max_requests
            });

          }
          if (this.info.day_to_open.length < 1) {

            this.info.day_to_open.push({
              id: 0,

              every_day: 0,
              every_time: 0,
              from_day: 0,
              from_time: 0,
              to_day: 0,
              to_time: 0,
              max_requests: 0
            });
          }

          this.getSubCategory();

        })
      },
      //         getDate_andTime(id){

