        this.deductionvalue = 0;
      },
      select_reser_type1(id) {

        this.reserv_type_id1 = id;
      },
      select_reser_type(id) {

        this.reserv_type_id = id;
        if (id == 1) {
          this.book_dur_status = true;
          this.book_num_status = false;
        } else if (id == 2) {
          this.book_dur_status = false;
          this.book_num_status = true;
        } else {
          this.book_dur_status = false;
          this.book_num_status = false;
        }
      },

      allowedHours(v) {

        return false;
      },

      comput_maxRequest(id, time_duration, from_tim, to_tim) {

        if (id == 0) {
          return 0;
        } else if (id == 1) {
          // this.reservation_duration

          var from_hour = parseInt(from_tim.split(":")[0])
          var from_min = parseInt(from_tim.split(":")[1])


          var to_hour = parseInt(to_tim.split(":")[0])
          var to_min = parseInt(to_tim.split(":")[1])

          var from = from_hour * 60 + from_min;


          var to = to_hour * 60 + to_min;

          var result = to - from;

          var houer = parseInt(time_duration.split(":")[0]);
          var minit = parseInt(time_duration.split(":")[1]);

          var dur = houer * 60 + minit;

          result = result / dur

          return result

        } else {
          return 0
        }




      },
      submit() {

        var imag = [];

        if (this.info.images.length < 1) {
          for (var im = 0; im < this.images.length; im++) {

            imag.push(this.images[im]);


          }
        } else {

          for (var im = 0; im < this.images.length; im++) {
            if (this.images[im] != null && this.images[im] != this.urL + "/" + this.info.images[im].image_path) {

              imag.push(this.images[im]);
            } else if (this.images[im] != null) {

            }

          }

        }

        if (this.info.day_to_open[0].every_day && this.info.day_to_open[0].every_time) {
          this.day_to_open.length = 0
          this.date_update = 1;
          this.day_to_open.push({

            every_day: 1,
            every_time: 1,
            from_day: 0,
            from_time: "00:00",
            to_day: 6,
            to_time: "24:00",
            max_requests: this.comput_maxRequest(this.reserv_type_id, this.reservation_duration, "00:00", "24:00")
          });

        }

        if (this.day_to_open.length == 0) {
          this.$swal('تحذير', "    يجب اختيار جدول الحجز", 'warning')
        }

        if (this.info.images.find(im => im === 'a')) {
          this.$swal('تحذير', " يجب اختيار 4 من الصور", 'warning')
        }


        if (this.$refs.form.validate() && this.day_to_open.length > 0) {

          var data = {

            province_id: this.info.address.province.id,
            country_id: this.info.address.province.country_id,
            category_id: this.info.sub_category.category_id,
            long: this.info.address.long,
            lat: this.info.address.lat,
            address_id: this.info.address.id,
            name: this.info.name,
            number: 0,
            discraption: this.info.disc,
            address_discraption: this.info.address.discraption,
            max_requests: this.info.max_requests,

            sub_category_id: this.info.sub_category.id,

            owner_id: "8",

            price: this.info.item_price.price,

            book_without_credit_card: this.info.item_price.book_without_credit_card,

            free_cancellation: this.info.item_price.free_cancellation,
            deduction: this.info.item_price.deduction,

            no_prepayment: "1",
            cancellation_deduction_ratio: "1",
            work_times: this.day_to_open,

            type: "png",
            images: imag.length != 0 ? imag : ["a", "a", "a", "a"],
            time_open_id: this.info.day_to_open[0].id,
            day_open_id: this.info.day_to_open[0].id,
            item_price_id: this.info.item_price.id,
            day_to_open_update: this.date_update
          };
          // this.description=JSON.stringify(data);

          this.loading = true;
          axios({
              method: 'post',
              url: this.urL + "/updateItem/" + this.itemId,
              data: data,
              headers: {
                token: this.token
              }

            })
            .then(response => {
              this.$swal('تم', "    تم تعديل الحجز", 'success')

              this.dialog = false;
              this.current_page = 0;
              this.getitems();
              this.$refs.form.reset();

              if (response.data.success == true) {
                this.$refs.form.reset()
                this.dialog = false;
                this.current_page = 0;
                this.getitems();
              }
              // 



            })
            .catch(error => {
              this.err_show = true;
              this.$swal(error, "     لم يتم اضافة الحجز", 'error')

            }).finally(d => {
              this.loading = false;
              this.everyday = false;
              this.everytime = false;
            });
        } else {

          this.err_show = true;




        }




      },
      getCategory() {

        var url = this.urL + "/getInfo/Category";
        axios.get(url).then(response => {
          this.category = response.data.Data;



        })
      },


      getImageData(url) {

        const image2base64 = require('image-to-base64');
        image2base64(url) // you can also to use url
          .then(
            (response) => {
              console.log(response); //cGF0aC90by9maWxlLmpwZw==
            }
          )
          .catch(
            (error) => {

            }
          )

      },

      getSubCategory() {

        var url = this.urL + "/getInfo/SubCategory/" + this.info.sub_category.category_id;
        axios.get(url).then(response => {
          this.subcategory = response.data.Data;



        })
      },
      getProvince() {

        var url = this.urL + "/getInfo/Province/1";
        axios.get(url).then(response => {
          this.province = response.data.Data;



        })
      },


      addday() {
        if (this.$refs.form_day.validate()) {


          this.msg_error = false;


          let search = this.day_to_open.find((o, i) => {
            if (o.from_day == this.days.indexOf(this.fromday)) {
              return o;
            }
          });


          if (search == undefined) {
            if (this.everytime && !this.everyday) {
              this.add_day_reversation();
            } else if (this.everyday && !this.everytime) {
              this.add_time_reversation();
            } else {
              this.add_dayTime_reversation();

            }
          } else {
            this.$swal('تحذير', "  لايمكن تكرار اليوم", 'warning')

            this.$refs.form_day.reset();
          }

        }

      },
      add_dayTime_reversation() {
        this.date_update = 1;

        this.day_to_open.push({
          every_day: this.everyday,
          every_time: this.everytime,
          from_day: this.days.indexOf(this.fromday),
          from_time: this.fromtime,
          to_day: this.days.indexOf(this.fromday),
          to_time: this.totime,
          max_requests: this.comput_maxRequest(this.reserv_type_id1, this.reservation_duration_foroneday, this
            .fromtime, this.totime)
        });

        this.$refs.form_day.reset();

      },
      add_day_reversation() {
        this.date_update = 1;
        this.day_to_open.push({
          every_day: this.everyday,
          every_time: this.everytime,
          from_day: this.days.indexOf(this.fromday),
          from_time: 0,
          to_day: this.days.indexOf(this.fromday),
          to_time: 23,
          max_requests: this.comput_maxRequest(this.reserv_type_id1, this.reservation_duration_foroneday, "00:00",
            "23:00")
        });

        this.$refs.form_day.reset();

      },
      add_time_reversation() {

        if (this.day_to_open.length < 1) {
          this.date_update = 1;
          this.day_to_open.push({
            every_day: this.everyday,
            every_time: this.everytime,
            from_day: 0,
            from_time: this.fromtime,
            to_day: 6,
            to_time: this.totime,
            max_requests: this.comput_maxRequest(this.reserv_type_id1, this.reservation_duration_foroneday, this
              .fromtime, this.totime)
          });

          this.$refs.form_day.reset();
        } else {
          this.$swal('تحذير', " لايمكن اضافة وقت اخر", 'warning')
        }


      },
      pickFile(index) {

        this.img_cliced = index
        this.$refs.image.click()
      },

      onFilePicked(e) {

        const files = e.target.files
        if (files[0] !== undefined) {
          this.imageName = files[0].name
          if (this.imageName.lastIndexOf('.') <= 0) {
            return
          }
          const fr = new FileReader()
          fr.readAsDataURL(files[0])
          fr.addEventListener('load', () => {
            this.imageUrl = fr.result
            this.imageFile = files[0]
            var imbase64 = fr.result;
            this.images[this.img_cliced] = imbase64;
            console.log(this.images)
            this.img_name = this.imageName
            //  this.info.images[this.img_cliced].image_path=fr.result



          })

        } else {
          this.imageName = ''
          this.imageFile = ''
          this.imageUrl = ''
        }
      },

      change_payment(status) {
        if (status == true) {
          this.free_cancelation = false;
        }
      },
      updatetime(item, index) {

        this.fromday = this.days[item.from_day];
        this.fromtime = item.from_time;
        this.totime = item.to_time;
        this.day_to_open.splice(index, 1);
      }
    },



    mounted() {
      this.token = localStorage.getItem('access_token');
      this.user_info = JSON.parse(localStorage.getItem('user_info'));


      this.getCategory();
      this.getProvince();
      this.getItemDetails();
    },
    components: {
      GoogleMap,
      itemForm
    }
  }
</script>