    <v-dialog v-model="dialog" width="500">
      <v-card>
       
      </v-card>
    </v-dialog>

    <v-row>

      <v-data-table :headers="headers" :items="rating" :page.sync="page" sort-by="calories"
        class="elevation-1 request_table" style="width:96%" items-per-page="15"  hide-default-footer=""
        :loading="loading" loading-text="جاري تحميل البيانات">

        <template v-slot:top>
          <v-toolbar flat color="white" pb-5>

            <v-toolbar-title>تقيم</v-toolbar-title>
            <v-divider class="mx-4" inset vertical></v-divider>
            <v-spacer></v-spacer>
            <v-row justify="end" style="margin-top:20px;">
             

         




            </v-row>

          </v-toolbar>
        </template>




        <template v-slot:bottom>
          <v-pagination v-model="page" :length="pageCount"></v-pagination>
        </template>
        <!-- edit reservation -->
       

        <!-- edit reservation -->
        <template v-slot:item.action="{ item }">

         <v-rating color="#f29c2b" background-color="gray" readonly="" size="25" v-model="item.digree">
              </v-rating>

        </template>
        <template v-slot:no-data>
          <h4>لاتوجد بيانات</h4>
        </template>



      </v-data-table>

    </v-row>
    <v-row>
      <v-pagination v-model="page" prev-icon="mdi-menu-right" next-icon="mdi-menu-left" circle="" :length="pageCount">
      </v-pagination>

    </v-row>
  </v-layout>

</template>


<script>
  export default {
    data: () => ({
    
   
      user_info: {},
      res_filter: '',
      token: localStorage.getItem('access_token'),
    
      urL: 'http://109.224.53.155',
      dialog: false,
      rating: [],     
      loding_accept: false,
      marvelHeroes: [],
      loding_cancel: false,
      headers: [{
          text: '#',
          align: 'center',
          sortable: false,
          value: 'id',
        },
        {
          text: 'اسم الحجز',
          align: 'center',
          sortable: false,
          value: 'item.name',
        },
        {
          text: 'اسم الزبون',
          align: 'center',
          sortable: false,
          value: 'user.name',
        },
        {
          text: 'التقييم',
          align: 'center',
          sortable: false,
          value: 'digree',
        },

         {
          text: 'الملاحظات',
          align: 'center',
          sortable: false,
          value: 'disc',
        },

       

        // {
        //   text: 'تاريخ الاضافة',
        //   value: 'created_at',
        //   align: 'center',
        //   sortable: false
        // },

        {
          text: ' ',
          value: 'status',
          align: 'center',
        },
        {
          text: ' ',
          value: 'action',
          align: 'center',
        },
      ],

      editedIndex: -1,
      editedItem: {
        name: '',
        calories: 0,
        fat: 0,
        carbs: 0,
        protein: 0,
      },

      defaultItem: {
        name: '',
        calories: 0,
        fat: 0,
        carbs: 0,
        protein: 0,
      },


      valid: false,
      page: 1,
      pageCount: 0,
      current_page: 1,
      last_page: 0,
      loading: false


    }),



    methods: {
     
     


      getitems() {
      var url = this.urL + "/GetOwnerItemRating?page="+this.current_page;
        this.loading = true;
        axios({
          method: 'get',
          url: url,
          headers: {
            token: this.token
          }

        }).then(response => {
          this.rating = response.data.Data.data;
         
          this.last_page = response.data.Data.last_page;
          this.pageCount = response.data.Data.last_page;







        }).catch(error => {
          this.$swal('خطاء', "خطاء بالاتصال", 'error')
        }).finally(s => {
          this.loading = false;

        });
      },


      getMoreitems() {
 

        if (this.current_page <= this.last_page) {
          this.current_page = this.page;
          this.getitems();
        }

      },





    },

    created() {
      this.getitems();
  
    },
    mounted() {
 


    },
    computed: {
       selected() {
        if (this.current_page != this.page) {

          this.getMoreitems();

        }
      }

   
    },


     watch: {
      selected: 'search by sub_cat_id',
    },
  }
</script>