        methods: {
            getItemDetails() {


                var url = this.urL + "/getItemBy/" + this.item_id;
                axios.get(url).then(response => {
                    this.editedItem = response.data.Data[0];
                    this.imgs = this.editedItem.images;
                    this.address = response.data.Data[0].address.province.province_name;

                    if (this.editedItem.images.length < 4) {
                        var completImgArrayLength = 4 - this.editedItem.images.length;

                        for (var i = 0; i < completImgArrayLength; i++) {

                            this.editedItem.images.push({
                                image_path: 'a'
                            });
                        }

                    }

                    this.getSubCategory();
                    for (var i = 0; i < 4; i++) {
                        if (this.editedItem.images[i].image_path != 'a') {
                            this.images[i] = this.urL + "/" + this.editedItem.images[i].image_path;
                        }

                    }

                    this.day_to_open = [];
                    for (var a = 0; a < this.editedItem.day_to_open.length; a++) {

                        this.day_to_open.push({

                            every_day: this.editedItem.day_to_open[a].every_day,
                            every_time: this.editedItem.day_to_open[a].every_time,
                            from_day: this.editedItem.day_to_open[a].from_day,
                            from_time: this.editedItem.day_to_open[a].from_time,
                            to_day: this.editedItem.day_to_open[a].to_day,
                            to_time: this.editedItem.day_to_open[a].to_time,
                            max_requests: this.editedItem.day_to_open[a].max_requests
                        });

                    }
                    if (this.editedItem.day_to_open.length < 1) {

                        this.editedItem.day_to_open.push({
                            id: 0,

                            every_day: 0,
                            every_time: 0,
                            from_day: 0,
                            from_time: 0,
                            to_day: 0,
                            to_time: 0,
                            max_requests: 0
                        });
                    }
                    this.everyday = this.day_to_open[0].every_day;
                    this.everytime = this.day_to_open[0].every_time;



                })
            },


            delete_img(img_id, index) {


                const Swal = require('sweetalert2');



                Swal.fire({
                    title: "هل انت متاكد من الحذف ؟",

                    icon: "warning",
                    showCancelButton: true,
                    confirmButtonColor: '#3085d6',
                    cancelButtonColor: '#d33',
                    confirmButtonText: 'Yes, delete it!'

                }).then((result) => {
                    if (result.value) {
                        this.img_cliced = index;

                        var url = this.urL + "/delete_item_img/" + img_id;
                        axios({
                            method: 'get',
                            url: url,
                            headers: {
                                token: this.token
                            }

                        }).then(response => {


                            this.editedItem.images[index].image_path = 'a';
                            //not important
                            this.img_name = index;
                            this.images[index] = 'a';

                            Swal.fire(
                                'تم الحذف بنجاح',
                                '',
                                'success'
                            )

                        }).catch(error => {
                            this.$swal('خطاء', "خطاء بالاتصال", 'error')
                        }).finally(d => {

                            this.getitems();
                        });



                    }
                })




            },


            cancel() {
                this.$refs.form.reset(),
                    this.$emit("changeStatus", false);
