            },
            step4() {
                this.e1 = 4;
                this.Check_Primery();
                this.Check_address();
                this.Check_date();
                // this.Check_payment();


            },
            step5() {
                this.e1 = 5;
                this.Check_Primery();
                this.Check_address();
                this.Check_payment();
                 this.Check_date();


            },

           

            Check_date()
            {
                this.errors_dates = [];
              
                if(this.day_to_open.length==0)
                {
                  
                this.errors_date.push("يرجئ تحديد الوقت");
                }
                else
                {
                    this.errors_date=[]
                }

               if (this.errors_date.length) {
                    this.er_date = false;
                } else {
                    this.er_date = true;
                }
            },


            Check_Primery() {




                this.errors_primery = [];
                
                if (!this.editedItem.name || !this.editedItem.sub_category.category_id || !this.editedItem.sub_category
                    .id || !this.editedItem.disc) {


                    this.errors_primery.push("يرجئ ملئ الحقول");
                }


                if (this.errors_primery.length) {
                    this.er1 = false;
                } else {
                    this.er1 = true;
                }


            },



            Check_address() {

                this.errors_address = [];
                if (!this.editedItem.address.country_id || !this.editedItem.address.province_id || !this.editedItem
                    .address.lat || !this.editedItem.address.long || !this.editedItem.address.discraption) {
                    this.errors_address.push("يرجئ ملئ الحقول");
                }
                if (this.errors_address.length) {
                    this.er_address = false;
                } else {
                    this.er_address = true;
                }
            },


            Check_payment() {
                this.errors_payment = [];
                if (!this.editedItem.item_price.price) {

                    this.errors_payment.push("يرجئ ملئ الحقول");
                }

                if (this.errors_payment.length) {
                    this.er_payment = false;
                } else {
                    this.er_payment = true;
                }
            },





            getlocation(lat, lng) {
                this.editedItem.address.lat = lat;
                this.editedItem.address.long = lng
                this.map_dialog = false;


            },
            price_percentage(v) {

                this.offer_info.new_price = this.offer_info.old_price;
                this.offer_info.new_price = this.offer_info.old_price - (this.offer_info.old_price * (v / 100));
                this.offer_info.discount_percentage = v;



            },







            change_timeday() {
                this.day_to_open.length = 0;
            },
            select_reser_type1(id) {

                this.reserv_type_id1 = id;
            },
            select_reser_type(id) {

                this.reserv_type_id = id;
                if (id == 1) {
                    this.book_dur_status = true;
                    this.book_num_status = false;
                } else if (id == 2) {
                    this.book_dur_status = false;
                    this.book_num_status = true;
                } else {
                    this.book_dur_status = false;
                    this.book_num_status = false;
                }
            },

            allowedHours(v) {

                return false;
            },

            comput_maxRequest(id, time_duration, from_tim, to_tim) {

                if (id == 0) {
                    return 0;
                } else if (id == 1) {
                    // this.reservation_duration

                    var from_hour = parseInt(from_tim.split(":")[0])
                    var from_min = parseInt(from_tim.split(":")[1])


                    var to_hour = parseInt(to_tim.split(":")[0])
                    var to_min = parseInt(to_tim.split(":")[1])

                    var from = from_hour * 60 + from_min;


                    var to = to_hour * 60 + to_min;

                    var result = to - from;

                    var houer = parseInt(time_duration.split(":")[0]);
                    var minit = parseInt(time_duration.split(":")[1]);

                    var dur = houer * 60 + minit;

                    result = result / dur

                    return result

                } else {
                    return 0
                }




            },
            submit() {

                var imag = [];
                for (var im = 0; im < this.images.length; im++) {
                    if (this.images[im] != 'a' && this.images[im] != this.urL + "/" + this.editedItem.images[im]
                        .image_path) {

                        imag.push(this.images[im]);
                    } else if (this.images[im] != 'a') {

                    }

                }

                if (!this.er1) {
                    this.step1();
                } else if (!this.er_address) {
                    this.step2()
                }
                    else if (!this.er_date) {
                    this.step3();
                } else if (!this.er_payment) {
                    this.step4();
                }

                if (this.editedItem.item_price.book_without_credit_card) {
                    this.editedItem.item_price.book_without_credit_card = 1;
                } else {
                    this.editedItem.item_price.book_without_credit_card = 0;
                }

                if (this.editedItem.item_price.free_cancelation) {
                    this.editedItem.item_price.free_cancelation = 1;
                } else {
                    this.editedItem.item_price.free_cancelation = 0;
                }



                if (this.everyday && this.everytime) {

                    this.day_to_open.length = 0
                    this.day_to_open.push({
                        every_day: this.everyday,
                        every_time: this.everytime,
                        from_day: 0,
                        from_time: "00:00",
                        to_day: 6,
                        to_time: "24:00",
                        max_requests: this.comput_maxRequest(this.reserv_type_id, this.reservation_duration,
                            "00:00", "24:00")
                    });





                }

                if (this.day_to_open.length == 0) {
                    this.$swal('تحذير', "    يجب اختيار جدول الحجز", 'warning')
                }



                if (this.images.find(im => im === 'a')) {
                    this.$swal('تحذير', " يجب اختيار 4 من الصور", 'warning');
                    return;
                }


                if (this.$refs.form.validate() && this.day_to_open.length > 0) {




                    var data = {


                        name: this.editedItem.name,
                        discraption: this.editedItem.disc,
                        sub_category_id: this.editedItem.sub_category.id,
                        category_id: this.editedItem.sub_category.category_id,
                        number: 0,
                        //for edit item
                        owner_id: 8,
                        address_id: 1,
                        time_open_id: this.item_id != 0 ? this.editedItem.day_to_open[0].id : 0,
                        day_open_id: this.item_id != 0 ? this.editedItem.day_to_open[0].id : 0,
                        item_price_id: this.item_id != 0 ? this.editedItem.item_price.id : 0,
                        //end for edit item
                        price: this.editedItem.item_price.price,
                        book_without_credit_card: this.editedItem.item_price.book_without_credit_card,
                        deduction: this.editedItem.item_price.deduction,
                        cancellation_deduction_ratio: 1,
                        free_cancellation: this.editedItem.item_price.free_cancelation,
                        no_prepayment: 1,








                        //Address Informations
                        province_id: this.editedItem.address.province_id,
                        country_id: this.editedItem.address.country_id,
                        address_discraption: this.editedItem.address.discraption,
                        long: this.editedItem.address.long,
                        lat: this.editedItem.address.lat,


                        work_times: this.day_to_open,
                        type: "png",
                        images: imag
                    };

                    // this.description=JSON.stringify(data);
                    this.loading = true;
                    var posturl = this.item_id != 0 ? this.urL + "/updateItem/" + this.item_id : this.urL + "/addItem";
                    axios({
                            method: 'post',
                            url: posturl,
                            data: data,
                            headers: {
                                token: this.token
                            }

                        })
                        .then(response => {

                            this.$swal('تم', this.item_id != 0 ? "تم تعديل الحجز" : "    تم اضافة الحجز", 'success')
                            this.dialog = false;


                            if (response.data.success == true) {

                                this.dialog = false;


                            }




                        })
                        .catch((error) => {
                        
                            this.err_show = true;
                            this.$swal('خطاء', this.item_id != 0 ? "     فشل  اضافة الحجز" : "فشل تعديل الحجز",
                                'error')

                        }).finally(d => {
                            this.loading = false;
                            this.everyday = false;
                            this.everytime = false;
                        });
                } else {

                    this.err_show = true;




                }




            },



            getCategory() {

                var url = this.urL + "/getInfo/Category";
                axios.get(url).then(response => {
                    this.category = response.data.Data;



                })
            },


            getCountry() {

                var url = this.urL + "/getInfo/Country";
                axios.get(url).then(response => {
                    this.countries = response.data.Data;



                })
            },



            getSubCategory() {

                var url = this.urL + "/getInfo/SubCategory/" + this.editedItem.sub_category.category_id;
                axios.get(url).then(response => {
                    this.subcategory = response.data.Data;



                })
            },
            getProvince() {

                var url = this.urL + "/getInfo/Province/1";
                axios.get(url).then(response => {
                    this.province = response.data.Data;



                })
            },


            addday() {
                if (this.$refs.form_day.validate()) {


                    this.msg_error = false;


                    let search = this.day_to_open.find((o, i) => {
                        if (o.from_day == this.days.indexOf(this.fromday)) {
                            return o;
                        }
                    });


                    if (search == undefined) {
                        if (this.everytime && !this.everyday) {
                            this.add_day_reversation();
                        } else if (this.everyday && !this.everytime) {
                            this.add_time_reversation();
                        } else {
                            this.add_dayTime_reversation();

                        }
                    } else {
                        this.$swal('تحذير', "  لايمكن تكرار اليوم", 'warning')

                        this.$refs.form_day.reset();
                    }

                }

            },


            add_dayTime_reversation() {


                this.day_to_open.push({
                    every_day: this.everyday,
                    every_time: this.everytime,
                    from_day: this.days.indexOf(this.fromday),
                    from_time: this.fromtime,
                    to_day: this.days.indexOf(this.fromday),
                    to_time: this.totime,
                    max_requests: this.comput_maxRequest(this.reserv_type_id1, this
                        .reservation_duration_foroneday, this
                        .fromtime, this.totime)
                });

                this.$refs.form_day.reset();

            },

            add_day_reversation() {
                this.day_to_open.push({
                    every_day: this.everyday,
                    every_time: this.everytime,
                    from_day: this.days.indexOf(this.fromday),
                    from_time: 0,
                    to_day: this.days.indexOf(this.fromday),
                    to_time: 23,
                    max_requests: this.comput_maxRequest(this.reserv_type_id1, this
                        .reservation_duration_foroneday, "00:00",
                        "23:00")
                });

                this.$refs.form_day.reset();

            },


            add_time_reversation() {

                if (this.day_to_open.length < 1) {
                    this.day_to_open.push({
                        every_day: this.everyday,
                        every_time: this.everytime,
                        from_day: 0,
                        from_time: this.fromtime,
                        to_day: 6,
                        to_time: this.totime,
                        max_requests: this.comput_maxRequest(this.reserv_type_id1, this
                            .reservation_duration_foroneday, this
                            .fromtime, this.totime)
                    });

                    this.$refs.form_day.reset();
                } else {
                    this.$swal('تحذير', " لايمكن اضافة وقت اخر", 'warning')
                }


            },
            pickFile(index) {

                this.img_cliced = index
                this.$refs.image.click()
            },

            onFilePicked(e) {

                const files = e.target.files
                if (files[0] !== undefined) {
                    this.imageName = files[0].name
                    if (this.imageName.lastIndexOf('.') <= 0) {
                        return
                    }
                    const fr = new FileReader()
                    fr.readAsDataURL(files[0])
                    fr.addEventListener('load', () => {
                        this.imageUrl = fr.result
                        this.imageFile = files[0]

                        this.images[this.img_cliced] = fr.result;
                        var a = this.img_cliced + 1;
                        this.img_name = "   صورة رقم " + a

                    })

                } else {
                    this.imageName = ''
                    this.imageFile = ''
                    this.imageUrl = ''
                }
            },

            change_payment(status) {
                if (status == true) {
                    this.editedItem.item_price.free_cancelation = false;
                    this.editedItem.item_price.deduction = 0;
                }

            },






            save() {
                if (this.editedIndex > -1) {
                    Object.assign(this.desserts[this.editedIndex], this.editedItem)
                } else {
                    this.desserts.push(this.editedItem)
                }
                this.close()
            },

        },

        watch: {
            selected: 'dd',
            steps(val) {

                if (this.e1 > val) {
                    this.e1 = val
                }
            },



        },
        computed: {

