<template>
<div>
 <v-toolbar flat color="white">
        <v-toolbar-title>الاقسام</v-toolbar-title>
        <v-divider
          class="mx-4"
          inset
          vertical
        ></v-divider>
        <v-spacer></v-spacer>
        <v-dialog v-model="dialog" max-width="500px">
          <template v-slot:activator="{ on }">
            <v-btn color="primary" dark class="mb-2" @click="addCat" v-on="on">اضافه قسم</v-btn>
          </template>
          
           <v-form ref="form" v-model="valid_search">
          <v-card style="padding:34px">
            <v-card-title>
            
            </v-card-title>

            <v-row class="mb-6" justify="center">
            <h4 style="color:#000">اضافة قسم </h4>
             <input type="file" style="display: none" ref="image" accept="image/*" @change="onFilePicked">
          </v-row>
          <v-row class="mb-2">

          </v-row>
          <v-layout column="" wrap>
            <v-flex pa-3>
              <v-text-field solo label="اسم القسم" v-model="name" :rules="nameRules" required></v-text-field>
            </v-flex>
        <v-flex pa-3>
              <v-text-field solo label="اسم الايقونة" v-model="icon" :rules="nameRules" required></v-text-field>
            </v-flex>
          </v-layout>
           <v-layout row wrap>

          
     
 <!-- <v-flex pa-3 xs2>
     <vfa-picker v-model="icon" >
    <template v-slot:activator="{ on }">
       
      <v-btn color="primary" @click="on">اختار ايقونة</v-btn>
    </template>
  </vfa-picker>
        
        </v-flex> -->
        
        
        <v-flex   >
    
      <span style="color:#fff">
      {{img_name}} </span>
        </v-flex>
          </v-layout>
<v-row justify="center" class="mb-6">
   <v-card height="200" width="200" class="card_img">
                      <v-img :src="images[0]" height="200" width="200" class="card_img" v-if="images[0]!='ab'">
                        <v-btn icon="" @click="cancelImg">
                          <v-icon 
                        >fas fa-window-close</v-icon>
                        </v-btn>

                      </v-img>

                      <v-btn icon v-else @click='pickFile()'>
                        <v-icon style="margin:0px" size="40">fas fa-plus-square</v-icon>
                      </v-btn>

                      <!-- <v-container fluid=""     style="width:100%;height:100%" @>
            
           </v-container> -->

                    </v-card>

</v-row>

      

            <v-card-actions>
               <v-btn color="success" v-if="loading" style="margin-left:10px"> <v-progress-circular indeterminate color="white"></v-progress-circular></v-btn>
                 <v-btn v-else color="success"  @click="submit" style="margin-left:10px">حفظ</v-btn>
                
               <v-btn color="red"  dark="" @click="close">الغاء</v-btn>
           
              
              <v-spacer></v-spacer>
             
            </v-card-actions>
          </v-card>
           </v-form>
        </v-dialog>
      </v-toolbar>
<v-container >
  

  <v-data-table
    :headers="headers"
    :items="category"
    sort-by="calories"
    class="elevation-1"
  >
    <template v-slot:item.edit="{ item }">
     
      <v-btn  icon="" @click="editCat(item)">
        <v-icon>fas fa-edit</v-icon>
      </v-btn>
     
    </template>
     <template v-slot:item.action="{ item }">
     
      <v-btn color="success"  :to="'/subCategory/'+item.id+'/'
      +item.name"   >عرض الاقسام الفرعية</v-btn>
     
    </template>
    <template v-slot:no-data>
        <v-btn color="primary" @click="getCategory">Reset</v-btn>
      </template>
  </v-data-table>
  </v-container>
</div>
</template>


<script>
const axios = require('axios');
//import { fontAwesomePicker } from 'font-awesome-picker'; 

  export default {
    data: () => ({
      dialog: false,
      valid_search:true,
      api:'/setCategory',
         v: undefined,
      headers: [
        {
          text: 'الرقم',
          align: 'center',
          sortable: false,
          value: 'id',
        },
        { text: 'اسم التصنيف', value: 'name',  align: 'center', },
        { text: 'وقت الانشاء', value: 'created_at',  align: 'center', },
        { text: ' ', value: 'edit',  align: 'center', },
         { text: ' ', value: 'action',  align: 'center', },
       
      ],
     token:'',
category:[],
       valid: false,
      icon:'',
        urL: 'http://109.224.53.155',

        nameRules: [
          v => !!v || 'هذا الحقل مطلوب  ',

        ],
        numberRul: [
          v => !!v || 'هذا الحقل مطلوب  ',

          v => !isNaN(v) || 'يجب ادخال رقم ',
        ],
        descriptRules: [
          v => !!v || 'هذا الحقل مطلوب  ',
        ],

      img_name:'',
        name: '',
        images:["ab"],
        loading:false
      
  }),

   

    watch: {
      dialog (val) {
        val || this.close()
      },
    },

   
    methods: {
cancelImg(){
this.images[0]='ab';
this.img_name='ghjk'

},



      addCat(){
        this.loading=false;
        this.name='';
        this.icon='';
this.api="/setCategory";
this.images=[];
this.images.push("ab");
      },
editCat(item){
  this.loading=false;

this.dialog=true;
this.name=item.name;
this.icon=item.icon;
this.images=[];
this.api="/UpdateCategory/"+item.id;
item.img_path!=null && item.img_path!="null" ? this.images.push(this.urL+"/"+item.img_path):this.images.push("ab");




        
},
  

      close () {
        this.dialog = false
        setTimeout(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        }, 300)
      },

   
       submit() {
       if(this.images[0].includes("http://")){
        this.images=[]
       }
if (this.$refs.form.validate()) {
     if(this.images[0]!="ab"){
this.loading=true;
     
     
         var data= {
               name:this.name,
               	icon:this.icon,
                 type:"png",
                 images:this.images,
                 subs:[{
                   name:this.name,
                   icon:this.icon,
                   images:this.images,}
                 ]
              };
              // this.description=JSON.stringify(data);
          axios({
              method: 'post',
              url: this.urL+this.api,
              data:data,
              headers:{token:this.token}
            })
            .then(response => {
this.$swal('', "    تم تحديث القسم", 'success')
          
if(response.data.success==true){
   this.$refs.form.reset()
}
             


             
this.dialog=false;

            this.getCategory()
            })
            .catch(error => {
              error;
 this.$swal('خطاء', "     لم يتم تحديث قسم", 'error')
this.loading=false;
             
            });
 }
 else{

   this.$swal('', "  يجب اضافة صورة للقسم", 'warning')
 }
 }


        

      },
         
        
      getCategory(){
                     
var url=this.urL+"/getInfo/Category";
   axios.get(url).then(response => {
       this.category=response.data.Data;



   })
      },

       pickFile() {

    
        this.$refs.image.click()
      },

      onFilePicked(e) {

        const files = e.target.files
        if (files[0] !== undefined) {
          this.imageName = files[0].name
          if (this.imageName.lastIndexOf('.') <= 0) {
            return
          }
          const fr = new FileReader()
          fr.readAsDataURL(files[0])
          fr.addEventListener('load', () => {
            this.imageUrl = fr.result
            this.imageFile = files[0]

            this.images[0] = fr.result;
            this.img_name=files[0].name;
           0

          })

        } else {
          this.imageName = ''
          this.imageFile = ''
          this.imageUrl = ''
        }
      },
      
     
    },
 mounted() {
    this.token= localStorage.getItem('access_token');
  this.getCategory();
  this.geticon
    },
    components: {
        //'font-awesome-picker': fontAwesomePicker,
       
    },
  }
</script>