<script>
  const Swal = require('sweetalert2');
  import mybookingCard from './sub_components/mybookingcard'
  export default {

    name: 'App',

    data: () => ({
     
      items: [],
      token: '',
      datafound: true,
      filterTitile: [{
          name: 'الكل',
          icon: 'fas fa-home'
        }, {
          name: 'الحجوزات المقبولة',
          icon: 'fas fa-check'
        },
        {
          name: 'الحجوزات المرفوضة ',
          icon: 'fas fa-window-close'
        }, {
          name: 'الحجوزات المعلقة',
          icon: 'fas fa-clock'
        }
      ],
      inactive: false,
      loading: true,
      taps_chaked: 3,

      user_info: {},
      num_cancel: -1,
      alert_msg: false,
      urL: 'http://109.224.53.155',
      text: " شركة انجاز هي شركة رائدة في مجال التحول من الأنظمة الورقية التقليدية الى الأنظمة الرقمية الالكترونية، حيث ان الشركة تقوم من خلال " +
        " تكنولوجيامتقدمة في وضع البنى التحتية الكفيلة بهذا الانتقال ومن ثم تقديم الخدمات بشكل الكتروني يضمن الرفاهية والسهولة ",


    }),
    methods: {

      ontap(index) {
        switch (index) {
          case 0:
            this.items = [];
            this.getitems();
            break;
          case 1:
            this.items = [];
            this.getMyreserById(2);
            break;


          case 2:
            this.items = [];
            this.getMyreserById(3);
            this.getMyreserById(4);

            break;




          case 3:
            this.items = [];
            this.getMyreserById(1);
            break;

        }


      },



      cancel_reservation(item, index) {


        var url = this.urL + "/UserCancelReservation/" + item.id;







        Swal.fire({
          title: "هل انت متاكد من الغاء الحجز ؟",

          icon: "warning",
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          confirmButtonText: 'نعم  ',
          cancelButtonText: 'الغاء',
          showClass: {
            popup: 'animated fadeInDown faster'
          },
          hideClass: {
            popup: 'animated fadeOutUp faster'
          }


        }).then((result) => {


          if (result.value == true) {
            this.num_cancel = index;

            axios({
              method: 'get',
              url: url,
              headers: {
                token: this.token
              }

            }).then(response => {
                this.getitems();
                  this.getitems();
              this.items[index].active = 0;
              item.active = 0;
              this.$swal('', "    تم الغاء الحجز", 'success')
            
             

            }).catch(error => {

              // this.msg = "يجب تسجيل الدخول"
              this.alert_msg = true;
              if (error.response.status == 402) {
                this.logout();
                this.$router.reload()
              }

            }).finally(


              this.num_cancel = -1,
              this.alert_msg = false,
              this.loading = false

            );
          }
        }); // end of swal confirm msg

      },

      getDateDiff(to_date, to_time) {


        var today = new Date();
        var from_time = today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds()
        var to_date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
        var timediff = require('timediff');

        var DateDiff = timediff('' + to_date + '  ' + from_time + '', '' + to_date + '  ' + to_time + '', 'YDHms');


    return DateDiff;
        // if(this.today<this.to_date)
        // {
        //    return DateDiff;
        // }
        
       
      },

      getMyreserById(reserId) {

        this.loading = true;
        var url = this.urL + "/getReserByStatusId/" + reserId;
        axios({
          method: 'get',
          url: url,
          headers: {
            token: this.token
          }

        }).then(response => {


          this.items.push.apply(this.items, response.data.Data);



          this.alert_msg = false;
          this.loading = false;

          if (this.items.length < 1) {

            this.datafound = false;
          } else {
            this.datafound = true;
          }
        }).catch(error => {
          this.msg = "يجب تسجيل الدخول"
          this.alert_msg = true;
          if (error.response.status == 402) {
            this.logout();
            this.$router.reload()
          }

        }).finally(

          this.loading = false

        );
      },




      getitems() {


        var url = this.urL + "/getUserReservations";
        axios({
          method: 'get',
          url: url,
          headers: {
            token: this.token
          }

        }).then(response => {
          this.items = response.data.Data.data;
          this.alert_msg = false;
          this.loading = false;

          //this.msg = "هنالك " + this.items.length + " حجوزات في الجدول";


          if (this.items.length < 1) {
            this.datafound = false
          } else {
            this.datafound = true;
          }

        }).catch(error => {
          //this.msg = "يجب تسجيل الدخول"
          this.alert_msg = true;
          if (error.response.status == 402) {
            this.logout();
            this.$router.reload()
          }

        }).finally(d => {

          this.loading = false;

        });
      },


      getDiscription(disc, len) {
        var text = disc;

        text = text.substring(0, len)


        return text;
      },

      logout() {
        this.$user.set({
          role: USER_ROLE_GUEST
        });

        window.localStorage.clear();

      }
    },
    mounted() {
      this.token = localStorage.getItem('access_token_user');
      this.user_info = JSON.parse(localStorage.getItem('user_info'));
      this.getitems();


    },
    components: {

      mybookingCard

    },
    created() {





    },


  };
</script>