      <v-btn  icon="" @click="editsubCat(item)">
        <v-icon>fas fa-edit</v-icon>
      </v-btn>
     
    </template>
    <template v-slot:no-data>
        <v-btn color="primary">Reset</v-btn>
      </template>
  </v-data-table>
</div>
</template>


<script>
  export default {
      props: ['itemm'],
    data: () => ({
      dialog: false,
      loading:false,
      headers: [
        {
          text: 'الرقم',
          align: 'center',
          sortable: false,
          value: 'id',
        },
        { text: 'اسم القسم الفرعي', value: 'name',  align: 'center', },
        { text: 'وقت الانشاء', value: 'created_at',  align: 'center', },
          { text: ' ', value: 'edit',  align: 'center', },
         { text: ' ', value: 'action',  align: 'center', },
       
      ],
      validate:true,
     
category:[],
info:{},
title:'',
id:'',
token:'',
api:"/setSubCategory",
       valid: false,
      
        urL: 'http://109.224.53.155',

        nameRules: [
          v => !!v || 'هذا الحقل مطلوب  ',

        ],
        numberRul: [
          v => !!v || 'هذا الحقل مطلوب  ',

          v => !isNaN(v) || 'يجب ادخال رقم ',
        ],
        descriptRules: [
          v => !!v || 'هذا الحقل مطلوب  ',
        ],

      
        name: '',
        img_name:'',
        images:["ab"]
      
  }),

   

    watch: {
      dialog (val) {
        val || this.close()
      },
    },

   
    methods: {
      

    addsubCat(){
        this.loading=false;
        this.name='';
       
this.api="/setSubCategory";
this.images=[];
this.images.push("ab");
      },
editsubCat(item){
  this.loading=false;

this.dialog=true;
this.name=item.name;

this.images=[];
this.api="/UpdateSubCategory/"+item.id;
item.img_path!=null && item.img_path!="null" ? this.images.push(this.urL+"/"+item.img_path):this.images.push("ab");




        
},

      close () {
        this.dialog = false
        setTimeout(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        }, 300)
      },

   
       submit() {
         if(this.images[0].includes("http://")){
        this.images=null
       }else{
        
       }
if (this.$refs.form.validate()) {
     if(this.images[0]!="ab"){ 
      this.loading=true;
         var data= {
               name:this.name,
                 category_id:this.id,
                 images:this.images,
                 type:"png"
              };
              // this.description=JSON.stringify(data);
          axios({
              method: 'post',
              url: this.urL+this.api,
              data:data,
             headers:{ token:this.token}
            })
            .then(response => {
 this.dialog=false;
if(response.data.success==true){
   this.$refs.form.reset();
   this.dialog=false;
   this.$swal('', "    تم اضافة القسم الفرعي", 'success')
}
             // 
this.getSubCategory();



             
            })
            .catch(error => {
 this.$swal('خطاء', "     لم يتم اضافة القسم الفرعي", 'error')
if(response.data.success==true){
   this.$refs.form.reset()
   this.dialog=false;
}
             
            }).finally(d=>{
              this.loading=false;
              
            });}
            else{
               this.$swal('', "  يجب اضافة صورة للقسم الفرعي", 'warning');
            }
 }else{
    this.$swal('', "  يجب اضافة صورefge الفرعي", 'warning')
 }


        

      },
      getSubCategory(){
                     
var url=this.urL+"/getInfo/SubCategory/"+this.id;
   axios.get(url).then(response => {
       this.category=response.data.Data;



   })
      },






        pickFile() {

    
        this.$refs.image.click()
      },

      onFilePicked(e) {

        const files = e.target.files
        if (files[0] !== undefined) {
          this.imageName = files[0].name
          if (this.imageName.lastIndexOf('.') <= 0) {
            return
          }
          const fr = new FileReader()
          fr.readAsDataURL(files[0])
          fr.addEventListener('load', () => {
            this.imageUrl = fr.result
            this.imageFile = files[0]

            this.images[0] = fr.result;
            this.img_name=files[0].name;
           0

          })

        } else {
          this.imageName = ''
          this.imageFile = ''
          this.imageUrl = ''
        }
      },
      
      
     
    },
 mounted() {
    this.token= localStorage.getItem('access_token');
    this.id= this.$route.params.id;
    this.title= this.$route.params.name;
  this.getSubCategory();
  
    },
  }
</script>