<template>

  <div>

    <div class="login-dark">
      <v-form ref="form" v-model="valid">
        <v-row>
          <div style="width:75px;height:90px;position: relative; right: 35%;">
            <v-img src="/assets/logo_black.png" style="with:100%;height:100%"></v-img>
          </div>
        </v-row>

        <br>

        <v-row class="mb-6" justify="center">

          <h3 style="color:black">تسجيل الدخول </h3>

        </v-row>





        <v-text-field v-model="user.phone" v-validate="'required'" label="رقم الهاتف" data-vv-name="name" type="number"
          :rules="phoneRul" required></v-text-field>


        <v-text-field v-model="user.password" v-validate="'required|max:10'" type="Password" label="كلمة المرور"
          :rules="passwordRules" data-vv-name="password" required></v-text-field>







        <v-row justify="center">
          <v-btn class="btn btn-primary btn-block" color="#ff6600" @click="submit" style=";color:#fff;width:100%">
            دخول</v-btn>

        </v-row>
        <v-row>
          <v-text-field color="primary" loading disabled v-show="loading"></v-text-field>
        </v-row>

        <p style="color:red;text-align:center">{{this.errors}}</p>
      </v-form>

    </div>
  </div>


</template>

<script>
  import {
    EventBus
  } from '../event-bus.js';
  import {
    USER_ROLE_GUEST,
    USER_ROLE_REGISTERED,
    ADMIN_ROLE,
    USER_ROLE,
    OWNER_ROLE,
    Delivery_ROLE

  } from "../constants.js";
  const axios = require('axios');
  export default {


    $_veeValidate: {
      validator: 'new',
    },
    name: 'app',



    data() {
      return {
        valid: true,
        login_info: [],
        validate: false,
        token: localStorage.getItem('access_token'),
        loading: false,
        urL: 'http://109.224.53.155',

        nameRules: [
          v => !!v || 'Name is requirsed',
          v => (v && v.length <= 10) || 'Name must be less than 10 characters',
        ],
        phoneRul: [
          v => !!v || 'رقم الهاتف مطلوب',
          // v => (v && v.length >10 && v.length <12) || 'رقم الهاتف يجب ان يكون ١١ رقم',
        ],
        passwordRules: [
          v => !!v || 'كلمة السر مطلبة',
          v => (v && v.length <= 10) || 'Password must be less than 10 characters',
        ],
        emailRules: [
          v => !!v || 'E-mail is required',
          v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
        ],

        errors: '',
        user: {
          phone: '',
          reg_dialog: false,
          password: '',
        },



        options: {
          isLoggingIn: true,
          shouldStayLoggedIn: true,
        },
      }
    },

    mounted() {



    },




    methods: {

      submit() {


        if (this.$refs.form.validate()) {
          this.loading = true;

          axios({
              method: 'post',
              url: this.urL + "/Auth/v1/login",
              data: {
                phone: "964" + parseInt(this.user.phone),
                password: this.user.password,
              }
            })
            .then(response => {

              this.login_info = response.data;


              if (this.login_info.user_role == "admin") {


                this.$user.set({
                  role: ADMIN_ROLE
                });


                this.$router.push({
                  name: "admin_dashbourd"
                });

                localStorage.setItem('ROLE_REGISTERED', this.$user.get().role);


                localStorage.setItem('access_token', response.data.token);
                localStorage.setItem('admin_info', JSON.stringify(response.data));






                //alert(this.$user.get().role);
              } else if (this.login_info.user_role == "owner") {


                this.$user.set({
                  role: OWNER_ROLE
                });


                localStorage.setItem('ROLE_REGISTERED', this.$user.get().role);


                localStorage.setItem('access_token', response.data.token);
                localStorage.setItem('admin_info', JSON.stringify(response.data));



                this.$router.push({
                  name: "OwnerDashbourd"
                });








              }

              //Delivery_ROLE
              else if (this.login_info.user_role == "Delivery_Company") {


                this.$user.set({
                  role: Delivery_ROLE
                });


                localStorage.setItem('ROLE_REGISTERED', this.$user.get().role);


                localStorage.setItem('access_token', response.data.token);
                localStorage.setItem('admin_info', JSON.stringify(response.data));



                this.$router.push({
                  name: "Delivery_dashbourd"
                });





              } else if (this.login_info.user_role == "client") {

                this.$router.push({
                  name: "login"
                });

              }





            })
            .catch(error => {
              // this.errors = error.response.data.error_description;
            }).finally(aa => {
              this.loading = false;

            });



        }

      },


    },
    components: {

    }
  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
  }
</style>